<template>
  <div class="about">
    <h1>This is an about page</h1>
    <button>{{ $t("govern.hello") }}</button>
    <h1>{{ $t("index.loginText") }}</h1>
    <h1 @click="change">{{ $t("index.change") }}</h1>
    <el-button @click="getETH">查询账户</el-button>
    <el-button @click="getMataMask">唤起小狐狸钱包</el-button>
  </div>
</template>

<script>
import Web3 from "web3";

export default {
  methods: {
    change() {
      this.$i18n.locale = "zh";
      console.log(this.$i18n.locale);
    },
    getMataMask() {
      if (window.ethereum) {
        window.ethereum.enable().then((res) => {
          alert("当前钱包地址:" + res[0]);
        });
      } else {
        alert("请安装MetaMask钱包");
      }
    },
    async getETH() {
      let web3 = new Web3(window.web3.currentProvider);
      // console.log(web3);
      let fromAddress = await web3.eth.getAccounts();
      // console.log(web3.eth.getBalance(fromAddress[0]));
      // console.log(fromAddress);
      web3.eth.getBalance(fromAddress[0], (err, res) => {
        if (!err) {
          alert("ETH余额==" + res / Math.pow(10, 18));
          //console.log(res)
        }
      });
    },
  },
};
</script>
